/* 字体大小 */
.nav ul {
  display: flex; }

.nav .nav-item {
  flex: 1;
  text-align: center;
  transition: all .25s ease 0s; }
  .nav .nav-item:active {
    background: #375fb0; }

.nav .on a {
  color: #fff; }

.nav a {
  display: block;
  color: rgba(255, 255, 255, 0.7); }

html {
  background: #f0f0f0; }

.main {
  margin: 1rem 0 1rem 0; }

.list .item {
  padding: .3rem .2rem;
  background: #fff;
  transition: all .25s ease 0s; }
  .list .item-hd {
    display: flex;
    align-items: center; }
  .list .item-bd {
    padding: .25rem 0;
    font-size: 0.36rem; }
  .list .item-ft {
    font-size: 0; }
  .list .item:active {
    background: #f8f8f8; }

.list .iconfont {
  padding-right: .1rem;
  font-size: 0.24rem; }

.list .user-avatar {
  width: .6rem;
  height: .6rem;
  margin-right: .1rem;
  background: #acb2c1;
  border-radius: 50%;
  overflow: hidden; }

.list .user-name {
  font-size: 0.34rem;
  line-height: 1; }

.list .put-top,
.list .put-good,
.list .topiclist-tab {
  padding: 0 .1rem;
  vertical-align: middle;
  display: inline-block;
  margin-right: .1rem;
  line-height: .36rem;
  font-size: 0.22rem; }

.list .put-top {
  color: red; }
  .list .put-top:after {
    border-color: red; }

.list .put-good {
  color: #4577dc; }
  .list .put-good:after {
    border-color: #4577dc; }

.list .topiclist-tab {
  color: #888f9b; }
  .list .topiclist-tab:after {
    border-color: #888f9b; }

.list .create-at {
  padding-top: .15rem;
  font-size: 0.24rem;
  color: #888f9b;
  line-height: 1; }

.list .replies,
.list .visits,
.list .last-active-time {
  line-height: 1;
  font-size: 0.24rem;
  color: #acb2c1;
  margin-right: .2rem; }

.list .replies,
.list .visits {
  padding-right: .2rem; }

.list a {
  display: block; }
